.verbal-score-body{
    background: #F1F3F6;
    padding: 20px;
    border-radius: 4px;

    .verbal-header{
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-size: 17px;
        color: #000;
        font-weight: 600;
    }


 .icon-div{
        height: 12px;
        width: 12px;
        background: #C69E5D;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        margin-left: 5px;
         }

         .verbal-body{
             margin: 20px 0;
         }

         .verbal-row-wrapper{
             display: flex;
             justify-content: space-between;
             align-items: center;
             margin-bottom: 10px;
         }


         .verbal-verbal-card{
             width: 85%;
         }

         .verbal-select{
             width: 10%;
             display: flex;
             justify-content: center;
           .verbal-colum-select{
               width: 78px;
           }
         }

         .verbal-total-score{
             display: flex;
            padding-top: 10px;
            justify-content: space-between;
            align-items: center;

             .verbal-verbal-card2{
                width: 85%;
                text-align: end;
                font-size: 17px;
                font-weight: 600;
                color: #000;
            }

            .verbal-score{
                width: 78px;
               display: flex;
               justify-content: center;
               align-items: center;
                font-size: 17px;
                font-weight: 600;
                color: #000;
                background: #fff;
                border: 1px solid #dadada;
                border-radius: 4px;
                height: 34px;
            }
         }
}