.mcq-score-body {
  background: #f1f3f6;
  padding: 20px;
  border-radius: 4px;

  .mcq-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 17px;
    color: #000;
    font-weight: 600;
  }

  .icon-div {
    height: 12px;
    width: 12px;
    background: #c69e5d;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    margin-left: 5px;
    font-size: 13px;
  }

  .mcq-body {
    margin: 20px 0;
  }

  .mcq-row-wrapper {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 10px;
  }

  .mcq-mcq-card {
    width: 85%;
  }

  .mcq-select {
    width: 10%;
    display: flex;
    justify-content: center;
    .mcq-colum-select {
      width: 78px;
    }
  }

  .mcq-total-score {
    display: flex;
    padding-top: 10px;
    justify-content: space-between;
    align-items: center;

    .mcq-mcq-card2 {
      width: 85%;
      text-align: end;
      font-size: 17px;
      font-weight: 600;
      color: #000;
    }

    .mcq-score {
      width: 78px;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 17px;
      font-weight: 600;
      color: #000;
      background: #fff;
      border: 1px solid #dadada;
      border-radius: 4px;
      height: 34px;
    }
  }
}
