.main-table-row {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 40px;
  border-bottom: 1px solid #dadada;
  font-weight: 500;
  font-size: 16px;

  .last-column {
    width: 160px;

    .MuiInputBase-root {
      font-size: 13px;
      border: none;
      padding: 0;
    }

  }

  .last-column-retake {
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .last-colum-select {
    width: 78px;
    [class$="-control"] {
      background: #f8f8f8;
    }

    .select-state__option {
      background: #f8f8f8 !important
        ;
    }
  }
  .weightage-input {
    width: 41px;
    font-size: 13px;
    height: 34px;


    .MuiOutlinedInput-input {
      padding: 5px;
      text-align: center;
    }
  }

  .weightage-input-error {
    width: 41px;
    font-size: 13px;
    height: 34px;

    .MuiInputBase-root {
      font-size: 13px;
      color: #ff4646;
    }

    .MuiOutlinedInput-input {
      padding: 5px;
      text-align: center;
    }
  }

  .no-of-questions {
    width: 70px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .question-type {
    width: 160px;
  }

  .retake-div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    // width: 115px;
  }

  .retake-select {
    margin-left: 10px;
  }
}
