.skip-main-assessment-table{

    .skip-assessment-table-header{
        display: flex;
        justify-content: space-between;
        background-color: #E9F3EF;
        align-items: center;
        // padding: 10px 80px 10px 40px;
        font-size: 17px;
        color: #000;
        font-weight: 600;
        border-radius: 4px;

        .skip-icon-div{
       height: 12px;
       width: 12px;
       background: #C69E5D;
       display: flex;
       justify-content: center;
       align-items: center;
       border-radius: 50%;
       margin-left: 5px;
        }
    }

    .skip-item-header-new{
        width: 140px;
        }

    .skip-select{
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .skip-total-score{
        display: flex;
       align-items: center;
       justify-content: flex-end;
       padding: 10px 45px;

        .skip-skip-card2{
           text-align: end;
           font-size: 17px;
           font-weight: 600;
           margin-right: 30px;
       }

       .skip-score{
           width: 62px;
          display: flex;
          justify-content: center;
          align-items: center;
           font-size: 17px;
           font-weight: 600;
           color: #000;
           background: #fff;
           border: 1px solid #dadada;
           border-radius: 4px;
           height: 39px;
          
       }
    }


        
    .edit-delete-new3{
        display: flex;
        justify-content: center;
        align-items: center;
        .icon-edit-delete{
            height: 20px;
            width: 20px;
            display: flex;
            justify-content: center;
            background: #D8DCE2;
            align-items: center;
            border-radius: 50%;
            cursor: pointer;
            margin-right: 10px;
        }
    }


}