.summary-main{

    .summary-heading{
        font-size: 16px;
        font-weight: 700;
        padding: 20px 0;
    }

    .verbal-questions-card{
       padding: 20px;
        background:#F1F3F6;
        border-radius: 4px;
        margin-top: 10px;
    }


    .card-class{
        margin-top: 10px;
    }

    .mcq-summary{
        margin-top: 10px;
    }

    .assessment-flow{
        margin: 20px 0;

        .assessment-flow-heading{
            font-size: 24px;
            font-weight: 600;
            margin-bottom: 10px;
        }
    }

    .score-card{
        margin: 10px 0;
    }

    .save-continue-back-button-div{
        margin-top: 20px;
      
        .save-continue-button{
            width: 224px !important;
            font-size: 13px !important;
        }
        .save-back-button{
            width: 224px !important;
            font-size: 13px !important;
            color: #04724d !important;
            background-color: #fff !important;
            border: 1px solid #04724d !important;
            margin-left: 10px !important;
        }
    }
}

.error-summary{
    text-align: right;
    font-size: 11px;
    color: red;
    height: 20px;
}