
.accordion-header{
    background: #E9F3EF;
    padding: 10px 20px;
    border-radius: 7px;

    .item-button{
        display: flex;
        justify-content: space-between;
    }
    
.first-flex-accordion{
    display: flex;
    align-items: center;

    .first-icon{
        margin-right: 20px;
    }
    
.accordion-heading{
    font-size: 14px;
    font-weight: 600;
}
.check-icon{
    padding: 0 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}
}

.second-flex-accordion{
    display: flex;
    align-items: center;

    .no-of-question,.total-time{
        font-size: 12px;
        font-weight: 600;
        padding: 0 10px;
    }

    .collapsible-icon-div{
        background: #04724d;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        height: 30px;
        width: 30px;
        margin-left: 40px;
    }

}
}


.accordion-panel{
    margin-top: 10px;
}

.edit-delete{
    display: flex;
    justify-content: center;
    align-items: center;
    .icon-edit-delete{
        height: 20px;
        width: 20px;
        display: flex;
        justify-content: center;
        background: #D8DCE2;
        align-items: center;
        border-radius: 50%;
        cursor: pointer;
        margin-right: 10px;
    }
}


