.main-assessment-table{

    .assessment-table-header{
        display: flex;
        justify-content: space-between;
        background-color: #E9F3EF;
        align-items: center;
        font-size: 17px;
        color: #000;
        font-weight: 600;
        border-radius: 4px;

        .icon-div{
       height: 12px;
       width: 12px;
       background: #C69E5D;
       display: flex;
       justify-content: center;
       align-items: center;
       border-radius: 50%;
       margin-left: 5px;
        }
    }

    .item-header-new{
    width: 210px;
    }

    .edit-delete-new{
        display: flex;
        justify-content: center;
        align-items: center;
        .icon-edit-delete{
            height: 20px;
            width: 20px;
            display: flex;
            justify-content: center;
            background: #D8DCE2;
            align-items: center;
            border-radius: 50%;
            cursor: pointer;
            margin-right: 10px;
        }
    }

   
}